<template>
    <transition name="slideu">
        <div class="container">
            <div class="heat-map-header slided">
                <p class="heat-map-title">{{title[charType]}}</p>
            </div>
            <operation-pane @listenOperationType="operationType"></operation-pane>
            <heat-map v-if="charType===0"></heat-map>
            <histogram v-if="charType===1"></histogram>
        </div>
    </transition>
</template>

<script>
import {fetchExcel} from '@/common/fetch/fetchExcelData'
import topBar from "@/components/top-bar";
import bottomBar from "@/components/bottom-bar";
// import operationPane from "@/components/operation-pane";
const operationPane = () => import('@/components/operation-pane')
// import heatMap from "@/components/heat-map"; 
const heatMap = () => import('@/components/heat-map')
// import histogram from '@/components/histogram';
const histogram = () => import('@/components/histogram')
import {mapMutations, mapState} from 'vuex'
export default {
    components: {
        operationPane,
        heatMap,
        topBar,
        bottomBar,
        histogram
    },
    data() {
        return {
            charType:0,
            title: ['污染物类型热力图', '污染物类型柱状图']
        };
    },
    created() {
        this.getExcelData();
    },
    methods:{
        operationType(data){
            this.charType = data
            console.log(data);
        },
        getExcelData() {
            fetchExcel('PM2d5').then(data => {
                this.savePM2d5(data)
            })
            fetchExcel('CO').then(data => {
                this.saveCO(data)
            })
            fetchExcel('NO2').then(data => {
                this.saveNO2(data)
            })
            fetchExcel('PM10').then(data => {
                this.savePM10(data)
            })
            fetchExcel('SO2').then(data => {
                this.saveSO2(data)
            })
            fetchExcel('O3').then(data => {
                this.saveO3(data)
            })
            fetchExcel('74city').then(data => {
                this.save74city(data)
            })
            fetchExcel('hotCityName').then(data => {
                this.saveHotcity(data)
            })
        },
        ...mapMutations({
            savePM2d5: 'savePM2d5',
            saveCO: 'saveCO',
            saveNO2: 'saveNO2',
            savePM10: 'savePM10',
            saveSO2: 'saveSO2',
            saveO3: 'saveO3',
            save74city: 'save74city',
            saveHotcity: 'saveHotcity',
        })
    }
};
</script>


<style scoped lang="scss">
.container {
  position: fixed;
  top: 70px;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 9999;
  overflow-y: auto;
  background-color: #fff;
}
.heat-map-header{
    position: fixed;
    display: flex;
    align-items: center;
    z-index: 10000;
    top: 70px;
    left: 0;
    height: 4rem;
    width: 100%;
    box-shadow: 0 0 4px 0 rgba(0,0,0,0.40);
    background-color: #fff;
    .heat-map-title{
        margin-left: 45px;
        padding-left: 25px;
        color:#32ADFE;
        font-size: 20px;
        border-left: 2px solid #32ADFE;
    }
}

</style>
